}

.page {
  scroll-behavior: smooth;
  width: 100%;
  height: 100vh;
  font-family: Manrope, sans-serif;
  overflow-x: hidden;
}

.page__body {
  margin: 0;
}

.page__section {
  margin-inline: 30px;
}

@media (width >= 768px) {
  .page__section {
    margin-inline: 34px;
  }
}

@media (width >= 1280px) {
  .page__section {
    margin-inline: 135px;
  }
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  transform: translateX(100%);
}

@media (width >= 1280px) {
  .page__menu {
    display: none;
  }
}

.page:before {
  content: "";
  opacity: 0;
  pointer-events: none;
  z-index: 1;
  background-color: #00000080;
  transition: opacity .3s;
  position: fixed;
  inset: 0;
}

.page:has(.page__menu:target) .page__menu {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.page:has(.page__menu:target):before {
  pointer-events: all;
}

.page:has(.page__menu:target) {
  overflow-y: hidden;
}

